export const linuxIntroHTML = `
<div id='write'  class=''><h1 id='linux-introduo-ao-sistema-operacional'><span>Linux: Introduo ao sistema operacional</span></h1><p><em><span>Anderson de Alencar Barros</span></em></p><h2 id='comandos-iniciais'><span>Comandos Iniciais</span></h2><ul><li><code>ls</code><span> lista diretrios e arquivos</span></li><li><code>ls -l</code><span> lista longa com detalhes</span></li><li><code>ls -s</code><span> lista com o tamanho alocado de cada arquivo  </span></li><li><code>ls -a</code><span> exibe arquivos e diretrios ocultos</span></li><li><code>ls &lt;dir&gt;</code><span> lista diretrio dado</span></li><li><code>history</code><span> histrico de comandos</span></li><li><code>mkdir</code><span> cria diretrio</span></li><li><code>rm</code><span> remover</span></li><li><code>rm -f</code><span> remove foradamente, ignorando arquivos no existentes ou argumentos</span></li><li><code>rm -r</code><span> remove diretrios recursivamente</span></li><li><code>rm -rf</code><span> remover diretrios no vazios</span></li><li><code>rmdir</code><span> remover diretrios vazios</span></li><li><code>cd &lt;dir&gt;</code><span> muda diretrio</span></li><li><code>~</code><span> pasta pessoal home</span></li><li><code>.</code><span> pasta atual</span></li><li><code>cd ..</code><span> retorna ao diretrio anterior</span></li><li><code>--help</code><span> exibe arquivo de ajuda</span></li><li><code>man</code><span> manual do comando</span></li><li><code>mv</code><span> mover </span></li><li><code>mv -i</code><span> confirmar antes de substituir</span></li><li><code>mv -n</code><span> sem substituio </span></li><li><code>mv -b</code><span> substituio pelo backup </span></li><li><code>mv -u</code><span> substitua se o arquivo de destino for antigo ou no existir</span></li><li><code>mv &lt;nome_antigo&gt; &lt;nome_novo&gt;</code><span> renomeia um arquivo</span></li><li><code>cp</code><span> copiar</span></li><li><code>cp -i</code><span> copia, porm verifica se o arquivo j existe</span></li><li><code>cp -v</code><span> exibe uma sada</span></li><li><code>cp -r</code><span> copia diretrios</span></li><li><code>cp -l</code><span> cria hard links </span></li><li><code>cp -s</code><span> cria links simblicos</span></li><li><code>cp -u</code><span> copia apenas quando o arquivo de destino for antigo ou no existir</span></li><li><code>clear</code><span> limpa terminal ou </span><code>CTRL + L</code></li><li><code>exit</code><span> sai do terminal </span></li></ul><h2 id='comandos-para-manipulao-de-arquivos-e-textos-e-redirecionamento'><span>Comandos para manipulao de arquivos e textos e redirecionamento</span></h2><ul><li><code>nano</code><span>  o editor do texto do terminal do Ubuntu</span></li><li><code>cat</code><span> imprime o arquivo .txt em ordem normal</span></li><li><code>tac</code><span> imprime o arquivo .txt em ordem inversa</span></li><li><code>head</code><span> imprime as primeiras 10 linhas de um ou mais arquivos</span></li><li><code>tail</code><span> imprime as ltimas 10 linhas de um ou mais arquivos</span></li><li><code>cal</code><span> mostra o calendrio</span></li><li><code>cal 2020</code><span> mostra o calendrio do ano de 2020 </span></li><li><code>cal december 2020</code><span> mostra o calendrio do ano de 2020 e o ms de dezembro </span></li><li><code>date</code><span> mostra a data e horrio</span></li><li><code>!!</code><span> repete o ltimo comando</span></li><li><code>&gt;&gt;</code><span> adiciona uma informao a um arquivo de texto</span></li><li><code>grep</code><span> faz uma busca por um termo dentro do texto</span></li><li><code>|</code><span> serve para usar mais de um comando</span></li><li><code>| more</code><span> ou </span><code>| less</code><span> servem para o arquivo ficar paginado, sem exibir totalmente</span></li><li><code>&amp;</code><span> executa dois ou mais comandos com sadas separadas</span></li><li><code>&amp;&amp;</code><span> executa dois ou mais comandos em sequncia com as sadas unidas</span></li><li><code>file</code><span> mostra o tipo de arquivo ou pasta</span></li><li><code>whatis</code><span> mostra o que faz determinado comando</span></li><li><code>find</code><span> busca um arquivo em um diretrio</span></li><li><code>find &lt;dir&gt; -type f -name &lt;nome&gt;</code><span> busca um arquivo (</span><code>-type f</code><span>) em um diretrio, o nome pode ser um padro em Regex</span></li><li><code>find &lt;dir&gt; -type d -name &lt;nome&gt;</code><span> busca por um diretrio (</span><code>-type d</code><span>)</span></li><li><code>touch</code><span> cria arquivos vazios </span></li><li><code>echo</code><span> imprime algo na sada</span></li></ul><h2 id='diretrios-do-linux-e-comandos-de-sistema'><span>Diretrios do Linux e Comandos de Sistema</span></h2><h3 id='diretrios'><span>Diretrios</span></h3><ul><li><code>/</code><span>  o diretrio raiz, todos os demais diretrios esto abaixo dele.</span></li><li><code>/bin</code><span> Contm arquivos programas do sistema que so usados com frequncia pelos usurios.</span></li><li><code>/boot</code><span> Arquivos estticos e gerenciador de inicializao.</span></li><li><code>/dev</code><span> Arquivos de dispositivos (perifricos).</span></li><li><code>/etc</code><span> Arquivos de configurao do sistema, especficos da mquina.</span></li><li><code>/home</code><span> Contm os diretrios dos usurios.</span></li><li><code>/lib</code><span> Bibliotecas essenciais compartilhadas e mdulos do kernel.</span></li><li><code>/mnt</code><span> Ponto de montagem para montar um sistema de arquivos temporariamente.</span></li><li><code>/proc</code><span> Diretrio virtual de informaes do sistema.</span></li><li><code>/root</code><span> Diretrio home do usurio root.</span></li><li><code>/sbin</code><span> Diretrio de programas usados pelo super usurio root, para administrao e controle do funcionamento do sistema.</span></li><li><code>/tmp</code><span> Arquivos temporrios.</span></li><li><code>/usr</code><span> Contm a maior parte de seus programas. Normalmente acessvel somente como leitura.</span></li><li><code>/var</code><span> Dados variveis, como: arquivos e diretrios de spool, dados de administrao e login, e arquivos transitrios.</span></li><li><code>/opt</code><span> Aplicativos adicionais e pacotes de softwares.</span></li></ul><h3 id='comandos-e-arquivos-do-sistema'><span>Comandos e arquivos do sistema</span></h3><ul><li><code>/proc/cpuinfo</code><span> contm todas as informaes da CPU</span></li><li><code>/proc/meminfo</code><span> contm todas as informaes da memria</span></li><li><code>/etc/passwd</code><span> contm todos os usurios do sistema</span></li><li><code>lspci</code><span> mostra informaes dos dispositivos PCI </span></li><li><code>lsusb</code><span> mostra informaes dos dispositivos USB</span></li><li><code>lscpu</code><span> mostra informaes do CPU</span></li><li><code>lshw</code><span> mostra de todo hardware que encontrar</span></li><li><code>lshw -short</code><span> mostra de todo hardware que encontrar resumidamente</span></li><li><code>arch</code><span> mostra qual a arquitetura do computador</span></li><li><code>uname</code><span> mostra o nome do kernel</span></li><li><code>uname -r</code><span> mostra a verso do kernel</span></li><li><code>uname -m</code><span> mostra a arquitetura do kernel</span></li><li><code>free</code><span> mostra a memria fsica e a memria swap</span></li><li><code>du -h</code><span> estima o espao ocupado por um diretrio </span></li></ul><h2 id='fundamentos-de-rede-e-comandos-de-rede'><span>Fundamentos de Rede e Comandos de Rede</span></h2><blockquote><p><span>Rede de computadores  um conjunto de equipamentos interligados de maneira a trocarem informaes e recursos.</span></p></blockquote><h3 id='tipos-de-redes'><span>Tipos de Redes</span></h3><ul><li><strong><span>Rede WAN</span></strong><span>, Wide Area Network ou World Area Network  uma rede geograficamente distribuda.</span></li><li><strong><span>Rede MAN</span></strong><span>, Metropolitan Area Network,  uma rede metropolitana que interligam vrias redes locais.</span></li><li><strong><span>Rede LAN</span></strong><span>, Local Area Network,  uma rede local de uma forma geral em um nico prdio, uma casa ou campus.</span></li></ul><h3 id='protocolos'><span>Protocolos</span></h3><p><strong><span>Protocolo</span></strong><span>  uma linguagem usada pelos dispositivos de uma rede de modo que eles consigam se entender.</span></p><ul><li><span>IP so nmeros que identificam seu computador em uma rede.</span></li><li><span>ICMP (Internet Control Message Protocol) tem o objetio prover mensagens de controle na comunicao entre ns.</span></li><li><span>DNS (Domain Name Server)  um protocolo de aplicao que tem por funo identificar endereos IP e manter uma tabela com endereos dos caminhos da rede.</span></li></ul><h3 id='interface-de-rede'><span>Interface de Rede</span></h3><p><span>Interface de Rede so softwares ou hardware que faz comunicao em uma rede.</span></p><p><span>As interfaces de rede no Linux esto em </span><code>/dev</code><span> e a maioria  criada dinamicamente pelos softwares quando requisitadas.</span></p><p><span>Exemplo: </span><code>eth0</code><span> placa de rede ethernet, </span><code>loopback</code><span> permite fazer conexes com si mesmo, permitindo testar programas de rede sem precisar interferir na sua rede, por padro, tem endereo IP </span><code>127.0.0.1</code><span>.</span></p><h4 id='comandos'><span>Comandos</span></h4><ul><li><code>ifconfig</code><span> mostra detalhes da redes ativas alm de configurar as interfaces de rede</span></li><li><code>hostname</code><span> mostra informaes do host, o nosso computador na rede</span></li><li><code>hostname -i</code><span> exibe IP do loopback</span></li><li><code>hostname -I</code><span> mostra o IP do computador</span></li><li><code>w</code><span> mostra informaes do usurio na rede</span></li><li><code>who</code><span> mostra como estamos logados</span></li><li><code>whoami</code><span> mostra o nome do usurio logado na rede</span></li><li><code>whois</code><span> traz vrias informaes sobre dado host, como dono, DNS, contato, e-mail e etc</span></li><li><code>ping</code><span> mostra se o host est ativo. Envia um pacote de requisio para o host e recebe as respostas</span></li><li><code>dig</code><span> mostra informaes sobre DNS</span></li><li><code>dig +short</code><span> mostra o IP do host dado</span></li><li><code>traceroute</code><span> traar a rota at determinado host, mostrando informaes dos ns at o host dado</span></li><li><code>finger</code><span> traz informaes detalhadas do usurio logado</span></li></ul><h2 id='fuando-no-linux-com-comandos-diversos'><span>Fuando no Linux com comandos diversos</span></h2><ul><li><code>history -c</code><span> limpa o histrico de comandos</span></li><li><code>alias</code><span> cria atalhos para comandos</span></li><li><code>nl</code><span> exibe o arquivo com o nmero de linhas </span></li><li><code>wc</code><span> exibe linhas, palavras, e letras de um arquivo</span></li><li><code>wc -l</code><span> mostra o nmero de linhas, conta as linhas em branco </span></li><li><code>wc -w</code><span> conta o nmero de palavras de um arquivo</span></li><li><code>cmp</code><span> compara arquivos </span></li><li><code>diff</code><span> compara arquivos e exibe o arquivo</span></li><li><code>sort -n</code><span> ordena as linhas de um arquivo, em ordem numrica</span></li><li><code>last reboot</code><span> exibe todas as informaes de reinicializao do sistema</span></li><li><code>route -n</code><span> mostra todas as tabelas de roteamento IP</span></li><li><code>time</code><span> mostra o tempo de um processo</span></li><li><code>uptime</code><span> mostra por quanto tempo o sistema est rodando</span></li><li><code>cowsay</code><span> exibe uma vaquinha que fala</span></li><li><code>cmatrix</code><span> exibe as letrinhas de Matrix</span></li><li><code>init 0</code><span> desliga a mquina</span></li><li><code>telinit 0</code><span> desliga a mquina</span></li><li><code>halt</code><span> desliga a mquina, todavia com autenticao</span></li><li><code>seq inicio fim</code><span> exibe uma sequncia de nmeros </span></li><li><code>whereis</code><span> exibe o caminho do programa e seu manual</span></li><li><code>which</code><span> exibe o local de um comando a ser executado no ambiente atual</span></li></ul><h2 id='controle-de-usurios-grupos-e-permisses'><span>Controle de usurios, grupos e permisses</span></h2><h4 id='usurios'><span>Usurios</span></h4><ul><li><code>adduser &lt;nome_usuario&gt;</code><span> adiciona novo usurio, s o root pode adicionar usurios</span></li><li><code>su &lt;nome_usuario&gt;</code><span> muda de usurio</span></li><li><code>passwd &lt;nome_usuario&gt;</code><span> muda a senha de um usurio</span></li><li><code>lastlog</code><span> exibe informaes de login de todos os usurios</span></li><li><code>last</code><span> listagem de entrada e sada de usurios no sistema</span></li><li><code>logname</code><span> exibe nome do usurio atual logado no sistema</span></li></ul><h4 id='grupos'><span>Grupos</span></h4><ul><li><code>id</code><span> exibe os identificadores do usurio e a que grupo pertence</span></li><li><code>/etc/passwd</code><span> j dito anteriormente, contm todos os usurios do sistema</span></li><li><code>/etc/group</code><span> contm todos os grupos do sistema</span></li><li><code>userdel -r &lt;nome_usuario&gt;</code><span> remove um usurio e sua pasta pessoal</span></li><li><code>groups</code><span> exibe todos os grupos que o usurio pertence</span></li><li><code>addgroup</code><span> cria um grupo</span></li><li><code>adduser &lt;nome_usuario&gt; &lt;grupo&gt;</code><span> adiciona um usurio a um grupo</span></li><li><code>gpasswd -a &lt;nome_usuario&gt; &lt;grupo&gt;</code><span> tambm adiciona um usurio a um grupo</span></li><li><code>gpasswd -d &lt;nome_usuario&gt; &lt;grupo&gt;</code><span> remove o usurio de um grupo</span></li><li><code>groupdel &lt;grupo&gt;</code><span> remove um grupo</span></li></ul><h4 id='permisses'><span>Permisses</span></h4><p><span>As permisses so divididas em 3 partes: o dono, grupo e outros cada um com 3 caracteres. Por exemplo, </span><code>drwxr-xr-x</code><span> mostra que nesse diretrio o dono tem permisso </span><code>rwx</code><span>,  o grupo </span><code>r-x</code><span> e os outros </span><code>r-x</code><span>. As letras indicam as permisses e o trao uma restrio, sendo </span><code>r</code><span> para ler, </span><code>w</code><span> para escrever e </span><code>x</code><span> para executar.</span></p><ul><li><code>ls -lh</code><span> exibe as permisses em um diretrio</span></li></ul><h5 id='comando-chmod-modo-arquivo-e-o-modo-octal'><span>Comando </span><code>chmod &lt;modo&gt; &lt;arquivo&gt;</code><span> e o Modo Octal</span></h5><p><span>O comando </span><code>chmod</code><span> altera as permisses de um arquivo ou diretrio, sendo possvel definir permisses para cada uma das partes, o dono, grupo e outros.</span></p><ul><li><span>Um nmero  definido para cada grupo</span></li><li><span>0 representa uma restrio</span></li><li><span>Para mais de uma permisso soma-se ambos. Por exemplo, para execuo e escrita em um arquivo qualquer para apenas o dono faz da seguinte maneira, </span><code>chmod 300 &lt;arquivo&gt;</code><span>, como tambm 777 daria todas as permisses possveis</span></li></ul><h2 id='compactao-descompactao-e-arquivamento'><span>Compactao, descompactao e arquivamento</span></h2><ul><li><code>gzip &lt;arquivo&gt;</code><span> compactador com taxa excelente de compactao, o arquivo original deixa de existir</span></li><li><code>gzip -9 &lt;arquivo&gt;</code><span> compactao com grau mximo</span></li><li><code>gunzip &lt;arquivo&gt;</code><span> para descompactar um arquivo </span><em><span>.gz</span></em></li><li><code>zip &lt;arquivo.zip&gt; &lt;arquivo&gt;+</code><span> compactador, porm mantm o arquivo original e pode ser colocado vrios arquivos</span></li><li><code>unzip &lt;arquivo.zip&gt;</code><span> para descompactar um arquivo </span><em><span>.zip</span></em></li><li><code>bzip2 &lt;arquivo&gt;</code><span> compactador mais atual que o </span><code>gzip</code><span> e </span><code>zip</code><span>, o arquivo original deixa de existir</span></li><li><code>bzip2 -d &lt;arquivo.bz2&gt;</code><span> para descompactar um arquivo </span><em><span>.bz2</span></em></li><li><code>rar a &lt;arquivo.rar&gt; &lt;arquivo&gt;+</code><span> gera um arquivo </span><em><span>.rar</span></em><span> e pode ser usado com vrios arquivos</span></li><li><code>rar x &lt;arquivo.rar&gt;</code><span> para descompactar um arquivo </span><em><span>.rar</span></em></li></ul><h4 id='arquivadores'><span>Arquivadores</span></h4><p><span>Junta vrios arquivos em um s. Pode ser usado em conjunto com um compactador para armazenar arquivos compactados. Um dos mais conhecidos  o </span><em><span>.tar</span></em><span>.</span></p><ul><li><code>tar -cf &lt;arquivo.tar&gt; &lt;arquivo&gt;+</code><span> cria um arquivo </span><em><span>.tar</span></em><span> com um ou mais arquivos. </span></li><li><code>tar -xvf &lt;arquivo.tar.gz&gt;</code><span> descompacta um arquivo </span><em><span>.tar.gz</span></em><span>, ou seja, um arquivo que foi empacotado com </span><code>tar</code><span> e em seguida, compactado com </span><code>gzip</code></li><li><code>tar -xvf &lt;arquivo.tar.gz&gt; -C &lt;dir&gt;</code><span> extrai para outro diretrio</span></li></ul><h2 id='gerenciamento-de-pacotes'><span>Gerenciamento de pacotes</span></h2><p><strong><span>Pacotes</span></strong><span> so programas colocados dentro de um arquivo identificados por sua extenso e incluem arquivos necessrios para sua instalao, como </span><em><span>.deb</span></em><span>, </span><em><span>.rpm</span></em><span> e outros.</span></p><p><strong><span>Gerenciadores de Pacotes</span></strong><span> so sistemas que possuem resoluo automtica de dependncias entre pacotes, mtodo fcil de instalao de pacotes, como dpkg, apt, yum.</span></p><ul><li><code>sudo apt install &lt;pacote&gt;</code><span> instala e atualiza um pacote</span></li><li><code>sudo apt upgrade &lt;pacote&gt;</code><span> atualiza um pacote </span></li><li><code>sudo apt remove &lt;pacote&gt;</code><span> remove um pacote </span></li><li><code>sudo apt update &amp;&amp; sudo apt upgrade</code><span> atualiza todos os pacotes </span></li></ul><h4 id='sites-de-pacotes'><span>Sites de Pacotes</span></h4><p><span>Locais onde se pode baixar pacotes</span></p><ul><li><a href='https://pkgs.org/'><span>pkgs.org</span></a></li><li><a href='http://rpm.pbone.net/'><span>rpm.pbone.net</span></a></li><li><code>sudo dpkg -i &lt;pacote.deb&gt;</code><span> instala um pacote </span><em><span>.deb</span></em><span> </span></li><li><code>sudo dpkg -I &lt;pacote.deb&gt;</code><span> obtm informaes de um pacote</span></li><li><code>sudo dpkg -r &lt;pacote&gt;</code><span> remove um pacote </span><em><span>.deb</span></em><span>, usa-se o nome do pacote dado nas informaes dele</span></li></ul><p><span>No Fedora,</span></p><ul><li><code>sudo rpm -ivh --nodeps &lt;pacote.rpm&gt;</code><span> instala um pacote e resolve as dependncias</span></li><li><code>sudo rpm -U &lt;pacote.rpm&gt;</code><span> atualiza um pacote </span></li><li><code>sudo rpm -e &lt;pacote.rpm&gt;</code><span> remove um pacote </span></li></ul><h2 id='shell-script'><span>Shell script</span></h2><p><span> uma interface de usurio para acessar os servios de um sistema operacional. Script  uma linguagem de programao que executa no sistema em tempo de execuo, muito usada em automao.</span></p><h4 id='variveis-de-ambiente'><span>Variveis de Ambiente</span></h4><ul><li><code>env</code><span> mostra as variveis de ambiente</span></li><li><code>portfolio=~/Documents/alencarbarros.github.io/</code><span> criando uma varivel de ambiente </span><code>portfolio</code><span> com um caminho</span></li><li><code>export portfolio</code><span> cria a varivel de ambiente</span></li><li><code>cd $portfolio</code><span>  possvel ir at esse caminho com tal varivel</span></li></ul></div></div>
`